.main-nav {
  position: relative; }

.main-nav__toggle {
  position: absolute;
  right: 20px;
  top: -45px;
  width: 25px;
  height: 19px;
  border: none;
  font-size: 0;
  background: none;
  z-index: 3; }
  .main-nav__toggle:hover {
    cursor: pointer; }
  .main-nav__toggle::before {
    position: absolute;
    top: 0;
    left: 0;
    display: block;
    content: "";
    width: 25px;
    height: 2px;
    background-color: #000000;
    box-shadow: 0 8px 0 0 #000000, 0 16px 0 0 #000000; }

.main-nav--toggled .main-nav__toggle::before {
  top: 8px;
  transform: rotate(45deg);
  box-shadow: none; }

.main-nav--toggled .main-nav__toggle::after {
  position: absolute;
  top: 8px;
  left: 0;
  display: block;
  content: "";
  width: 25px;
  height: 2px;
  background-color: #000000;
