.copyright {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between; }

.copyright__text {
  margin-top: 12px;
  font-family: "Arial", sans-serif;
  font-size: 16px;
  color: #444444; }

.copyright svg {
  fill: #666666; }

.copyright:hover svg {
  fill: #68b738;
  transition: 200ms ease-out; }

.copyright:active {
  opacity: 0.4; }

@media (min-width: 768px) {
  .copyright {
    width: auto;
    justify-content: flex-start; }
